export function BlockTextWithIcon({title, text, icon}) {

  return (
    <div className="block">
      <div className="block__icon" style={{backgroundImage: `url(${icon})`} }></div>
      <span className="block__title">{title}</span>
      <p className="block__text">{text}</p>
    </div>
  )
}

